@mixin responsive($device) {

  @if $device == phone {
    @media (max-width: 37.5em) { @content};
  }
  @if $device == tab-port{
    @media (max-width: 56.25em) { @content};
  }
  @if $device == tab-land {
    @media (max-width: 75em) { @content};
  }
  @if $device == big-desktop {
    @media (min-width: 112.5em) { @content};
  }
}

@mixin clearfix {
  &::after {
    content: '';
    display: table;
    clear: both;
  }
}